body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.grid-layout-3columns {
  display: grid;
  grid-template-columns: 33% auto 30%;
  grid-template-rows: auto auto auto;
  grid-gap: 2px; }
  .grid-layout-3columns pre {
    background-color: lemonchiffon; }

.json-display {
  height: 600px;
  width: 430px; }

.basic-grid-1 {
  font: 13px "Lucida Sans Unicode", "Lucida Grande", sans-serif;
  border: 2px solid green;
  display: grid;
  grid-template-columns: auto auto auto;
  grid-template-rows: auto auto auto;
  grid-gap: 2px; }
  .basic-grid-1 .x-error {
    margin: 0; }
    .basic-grid-1 .x-error small {
      color: red; }
      .basic-grid-1 .x-error small ul {
        min-height: 15px;
        margin: 0px;
        padding-top: 3px;
        padding-left: 1.5em; }
  .basic-grid-1 .x-header {
    font-size: 1.5em;
    font-weight: bold;
    color: green; }
  .basic-grid-1 > div {
    margin-left: 10px; }
  .basic-grid-1 input, .basic-grid-1 select {
    margin-top: 2px; }

